.user-page .title {
    font-size: 30px;
    margin-bottom: 30px;
}

.user-page .item {
    margin-bottom: 30px;
}

.user-page .row {
    display: flex;
    width: 100%;
}

.user-page .left {
    margin-right: 15px;
    text-align: center;
    min-width: 90px;
    max-width: 90px;
}

.user-page .img img {
    width: 70px;
    height: 70px;
    margin-bottom: 5px;
    border-radius: 50%;
    object-fit: cover;
}

.user-page .followed {
    width: 100%;
    cursor: pointer;
    user-select: none;
    padding: 5px;
    background-color: green;
    color: white;
    font-size: 20px;
    box-sizing: border-box;
    border: none;
    text-align: center;
    transition: color 0.3s ease 0s, background 0.3s ease 0s, opacity 0.3s ease 0s;
}

.user-page .followed.disabled {
    pointer-events: none;
    opacity: 0.5;
    background: #ccc;
    color: #000;
}

.user-page .right {
    border: 1px solid gray;
    border-radius: 15px;
    width: 100%;
    padding: 10px;
}

.user-page .subrow {
    display: flex;
    justify-content: space-between;
    height: 100%;
}

.user-page .info {
    display: flex;
    flex-direction: column;
}

.user-page .name {
    flex: 1 0 auto;
    font-size: 25px;
}

.user-page .status {
    font-size: 18px;
}

.user-page .country {
    font-size: 22px;
    line-height: 25px;
    margin-bottom: 5px;
    text-align: right;
}

.user-page .city {
    font-size: 18px;
    text-align: right;
}

.user-page .cover {
    display: flex;
    justify-content: center;
}

.user-page .btn {
    display: inline-block;
    cursor: pointer;
    user-select: none;
    padding: 10px 15px;
    background-color: green;
    color: white;
    font-size: 22px;
    box-sizing: border-box;
}

.user-page .pag {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 30px;
}

.user-page .pag__item {
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    font-size: 22px;
    line-height: 20px;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #fff;
    margin: 10px;
    transition: all 0.3s ease 0s;
}

.user-page .pag__item.active {
    font-weight: 800;
    border-radius: 50%;
}
.user-page .preload img {
    width: 60px;
    height: 60px;
}
