.login .title {
    font-size: 30px;
    margin-bottom: 30px;
}

.login .label {
    display: block;
    margin-bottom: 30px;
}

.login input:not(input[type="checkbox"]) {
    width: 300px;
    height: 40px;
    font-size: 20px;
    padding: 0px 5px;
    box-sizing: border-box;
}

.login span {
    font-size: 20px;
}

.login .btn {
    cursor: pointer;
    user-select: none;
    font-size: 20px;
    padding: 5px 30px;
}

.login .form__error {
    display: block;
    max-width: 300px;
    color: red;
    font-size: 18px;
    border: 2px solid red;
    margin-bottom: 10px;
    padding: 5px;
    box-sizing: border-box;
}
