.sidebar {
    width: calc(30% - 30px);
    background-color: green;
}

.sidebar .nav {
    display: flex;
    flex-direction: column;
    padding: 30px;
}

.sidebar .nav a {
    font-size: 25px;
    line-height: 30px;
    color: #fff;
    margin-bottom: 15px;
    text-decoration: none;
}

.sidebar .nav a.active {
    color: gold;
}
