.page {
    background-color: #282c34;
    min-height: 100vh;
    font-size: calc(10px + 2vmin);
    color: white;
}

.container {
    max-width: 1200px;
    margin: 0px auto;
}

.header {
    width: 100%;
    margin-bottom: 30px;
}

.header__body {
    height: 60px;
    background-color: green;
    display: flex;
    align-items: center;
}

.header img {
    display: block;
    width: 40px;
    height: 40px;
    object-fit: cover;
}

main {
    min-height: 500px;
}

.main {
    height: 100%;
    display: block;
}

.main .container {
    height: 100%;
    display: flex;
    justify-content: space-between;
}

.page-content {
    width: 70%;
}
